html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
/* universal reset */
* {
  vertical-align: baseline;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  border: 0 none;
  outline: 0;
  padding: 0;
  margin: 0;
}
/*
* {
  outline: 2px dotted red
}
* * {
  outline: 2px dotted green
}
* * * {
  outline: 2px dotted orange
}
* * * * {
  outline: 2px dotted blue
}
* * * * * {
  outline: 1px solid red
}
* * * * * * {
  outline: 1px solid green
}
* * * * * * * {
  outline: 1px solid orange
}
* * * * * * * * {
  outline: 1px solid blue
}
*/
body {
  position: relative; /* for overlay effect */
  width: auto; /* prevent horizontal scrolling on mobile */
  overflow-x: hidden; /* prevent horizontal scrolling on mobile */
}
ul {
  padding-left: 40px;
}
.container {
  overflow-x: hidden; /*prevent horizontal scrolling on mobile*/
}
/* used by tinymce WP editor */
img.centered, .aligncenter, div.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
img.alignright {
  padding: 4px;
  margin: 0 0 2px 7px;
  display: inline;
}
img.alignleft {
  padding: 4px;
  margin: 0 7px 2px 0;
  display: inline;
}
.alignright {
  float: right;
}
.alignleft {
  float: left;
}
/* end used by tinymce WP editor */

.overlay-on {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5); /*dim the background*/
}
.overlay-off {
  position: absolute;
  width: 0;
  height: 0;
}
/* paragraph spacing */
p {
  margin: 0;
}
p + p {
  margin-top: 1.0em;
}
.text-center {
  text-align: center;
}
#header{
  ul{
    padding: 0;
    margin: 0;
    > li{
      height: auto;
      float: left;
      text-align: center;
      list-style: none;
      padding: 0;
      margin: 0;
      position: relative;
      padding: 0;
      &:hover ul{
        display: block;
      }
      ul{
        display: none;
        height: auto;
        margin: -15px;
        margin-top: -20px;
        padding: 0;
        position: absolute;
        li{
          list-style: none;
          text-align: left;
          position: relative;
          a{
            color:#000;
            &:hover{
              color:#000;
            }
          }
        }
      }
    }
  }
}
